<template>
  <div class="Auth">
    <span class="authSpan"> 로그인을 진행해주세요. </span>
    <form v-if = "Step == 2"
          @submit.prevent="onSubmitSignup"
          class="SignupForm">
      <span> Sign Up </span> 
      <br/>
      <input type="email"
              placeholder="Email" 
              v-model="email"
              required>
      <input type="password"
              placeholder="Password" 
              v-model="password"
              required>
      <div>
      <input type="text"
              placeholder="Display name"
              v-model="displayName"
              required />
      </div>
      <button> OK </button>
    </form>

    <form v-if = "Step == 1"
          @submit.prevent="onSubmitLogin"
          class="LoginForm">
      <span> Log In </span>  
      <br/>
      <input type="email"
              placeholder="Email" 
              v-model="email"
              required>
      <input type="password"
              placeholder="Password" 
              v-model="password"
              required>
      <button>  OK </button>
    </form>
  </div>
</template>

<script setup>
import { defineProps, ref } from 'vue';
import { createUserWithEmailAndPassword,
         signInWithEmailAndPassword } from "firebase/auth";
import { doc, setDoc } from '@firebase/firestore'
import { db, auth } from '@/firebase/firebase.js'

const email = ref('')  
const password = ref('') 
const displayName = ref('') 
const uid = ref('')  
const userinfo = ref([])

const onSubmitSignup = async(event) => {
  event.preventDefault()
  await createUserWithEmailAndPassword(auth, email.value, password.value)
    .then((userCredential) => {
    alert('회원가입이 완료되었습니다.')
      console.log("user info : " + userCredential.user.email)
      uid.value = userCredential.user.uid
    })
    await setDoc(doc(db, 'Users', uid.value), {
        Email: email.value,
        DisplayName: displayName.value,
        Date: new Date(),
        UID: uid.value,
    })
      email.value = '' 
      password.value = '' 
      displayName.value =''
}

const onSubmitLogin = async(event) => {
event.preventDefault();
await signInWithEmailAndPassword(auth, email.value, password.value)
    .then((userCredential) => {
    alert('로그인이 완료되었습니다.')
    // const user = userCredential.user;
    // console.log("Email : " + userCredential.user.email)
    console.log("user info " + userCredential.user.email)
    console.log(userCredential.user)
    userinfo.value = userCredential.user
    })
    email.value = '' ;
    password.value = '' ;
} 


const props = defineProps({
  Step: Number,
})

console.log(props.Step); 
</script>

