<template>
    <form @submit.prevent="onSubmit" class="inputForm">
        <div>
            <input type="text" 
                   placeholder="message" 
                   required
                   v-model="message"/>
            <button> Send </button>
        </div>
    </form>
</template>

<script setup> 
import { defineProps, ref } from 'vue' 
import { db } from '@/firebase/firebase'
import { arrayUnion, doc, updateDoc } from '@firebase/firestore'

const message = ref('')
const onSubmit = async(event) => {
    event.preventDefault()
    const CurrentUserMessge = doc(db, 'Users/' + props.CurrentUser.UID + '/Message/' + props.CurrentMessageDocID)
    await updateDoc(CurrentUserMessge, {
        regions: arrayUnion({
            counterpartUID : props.counterpartUID, 
            // CurrentUserUID : props.CurrentUser.UID, 
            sendMSG : message.value,
            sendNAME : props.CurrentUser.DisplayName,
            sendDATE : Date.now(),
        })
    })
    const CounterparMessge = doc(db, 'Users/' + props.counterpartUID + '/Message/' + props.CounterpartMessageDocID)
    await updateDoc(CounterparMessge, {
        regions: arrayUnion({
            // counterpartUID : props.CurrentUser.UID,
            CurrentUserUID : props.counterpartUID, 
            sendMSG : message.value,
            sendNAME : props.CurrentUser.DisplayName,
            sendDATE : Date.now(),
        })
    })
    message.value = ''
}

// Input data save 구현 

const props = defineProps({
  CurrentUser: Object,
  counterpartUID : String, 
  CurrentMessageDocID : String, 
  CounterpartMessageDocID : String, 
})

</script>

