<template>
  <div class="AppSide">
    <div class="header">
      <h4> VueKao Talk </h4> 
      <h6> {{CurrentUser.DisplayName}} </h6>
    </div>
    <div class="authForm" v-if="hiden == false">
      <Auth :Step = "step"/>
      <button @click = "step = 2" class="signupBtn"> SignUp </button>
      <button  @click = "step = 1" class="loginBtn"> LogIn </button>
    </div>
    <div v-if="hiden == true" class="chatlist">
      <div v-if = "step == 3" >
        <ChatList :CurrentUser = "CurrentUser"/>
        <button @click.prevent="onClickLogOut"> Log Out </button>
      </div>
    </div>
  </div>
</template>

<script setup>
import Auth from './components/Auth.vue'
import ChatList from './components/ChatList.vue'
import { ref, onMounted } from 'vue' 
import { db, auth } from '@/firebase/firebase.js'
import { query, where, onSnapshot, collection } from 'firebase/firestore'
import { signOut } from '@firebase/auth'

const step = ref(0)
const hiden = ref(true)
const loggedin = ref(false)
const title = ref('')
const CurrentUser = ref([])
const UserAllInfo = ref([])

onMounted(() => {
    auth.onAuthStateChanged((user) => {
    if(user != null) {
      loggedin.value = true
      hiden.value = true
      title.value = 'welcome'
        onSnapshot(query(collection(db, "Users"), where("UID", "==", user.uid)),
          (querySnapshot) => {
          querySnapshot.forEach((doc) => {
            CurrentUser.value = doc.data()
          })
          console.log(UserAllInfo.value)
        })
      step.value = 3
      } else {
        loggedin.value = false
        hiden.value = false
        title.value = 'please login'
      }
  })
})

const onClickLogOut = () => {
  signOut(auth) 
}


// css 수정 필요, min width, min height 추가 필요.
</script>

