<template>
  <div class="MSG">
    <div v-for="(msg, i) in MessageDoc" :key="i">
      <ul  v-if="msg.CurrentUserUID" class="chat-content"> 
        <li><span class="owner-chat-box xmine">{{msg.sendMSG}}</span></li>
      </ul>
      <ul  v-if="msg.counterpartUID" class="chat-content"> 
        <li><span class="chat-box mine">{{msg.sendMSG}}</span></li>
      </ul>
    </div>
  </div>
</template>

<script setup>
import { db } from '@/firebase/firebase'
import { collection, onSnapshot, query, where, orderBy } from '@firebase/firestore'
import { defineProps, onMounted, ref } from 'vue' 

const MessageDoc = ref([])

onMounted(() => {
  onSnapshot(
      query(collection(db, 'Users/' + props.CurrentUser.UID + '/Message'), 
      where("ChatUserUID", "array-contains", props.counterpartUID)),
      orderBy('sendDate', 'desc'),
      (snapshot) => {
        snapshot.forEach((doc) => {
        MessageDoc.value = doc.data().regions
        console.log(doc.data())
      })
  })
})

const props = defineProps({
  CurrentUser: Object,
  counterpartUID : String, 
})

// console.log(props.CurrentUser)
// console.log(props.counterpartUID)
</script>

