<template>
  <div class="ChatList">
    <form @submit.prevent="onSubmit" v-if= "hiden == false">
    <h4> User List </h4>
        <div v-for="(UserList, i) in UserListName" :key="i">
            <div v-if="UserList.DisplayName !== props.CurrentUser.DisplayName"
                class="chatRoom">
                <h5> {{UserList.DisplayName}} 
                    <button @click.prevent="onClickHiden" class="btnUID"> 
                        {{UserList.UID}} 
                    </button>
                </h5>
            </div>
        </div>
    </form>

        <div v-if= "hiden == true" class="chatMessage">
            <h4> {{DisplayName}} </h4>
            <button v-if="!CurrentMessageDocID" @click.prevent="onClick" 
                class="startChatBtn"> 채팅방 개설하기 </button>
                <div v-if="CurrentMessageDocID">
                    <div class="MsgForm">
                        <ChatMSG :CurrentUser = "CurrentUser"
                            :counterpartUID = "counterpartUID"/>
                    </div>
                    <div class="InputForm">
                        <Input :CurrentUser = "CurrentUser"
                            :counterpartUID = "counterpartUID"
                            :CurrentMessageDocID = "CurrentMessageDocID"
                            :CounterpartMessageDocID = "CounterpartMessageDocID"/>
                    </div>
                </div>
            <button @click.prevent="onClickBack"
                    class="backButton"> 이전 </button>
        </div>
  </div>
</template>

<script setup>
import ChatMSG from '/Users/drizzle/vue-kao-talk/src/components/ChatMSG.vue'
import Input from '/Users/drizzle/vue-kao-talk/src/components/Input.vue'
import { ref, onMounted, defineProps } from 'vue' 
import { db } from '@/firebase/firebase.js'
import { addDoc, collection, doc, getDoc, getDocs, onSnapshot, query, where } from 'firebase/firestore'

const hiden = ref(false)
const UserListName = ref([])
const counterpartUID = ref('')
const DisplayName = ref('')
const CurrentMessageDocID = ref('')
const CounterpartMessageDocID = ref('')

onMounted(async() => {
    const querySnapshot = await getDocs(collection(db, "Users"));
    let firebaseUserList = []
    querySnapshot.forEach((doc) => {
        const UserList = {
            docID : doc.id, 
            Date : doc.data().Date,
            DisplayName : doc.data().DisplayName,
            Email : doc.data().Email,
            UID : doc.data().UID,
        }
        firebaseUserList.push(UserList)
    });
    UserListName.value = firebaseUserList
})

const onClickHiden = async(event) => {
    CurrentMessageDocID.value = ''
    counterpartUID.value = ''
    hiden.value = true
    counterpartUID.value = event.target.innerText

    onSnapshot(
        query(collection(db, 'Users/' + props.CurrentUser.UID + '/Message'), 
        where("ChatUserUID", "array-contains", counterpartUID.value)),
        (snapshot) => {
            snapshot.forEach((doc) => {
                CurrentMessageDocID.value = doc.id
                console.log(doc.data())
        })
    })
    onSnapshot(
        query(collection(db, 'Users/' + counterpartUID.value + '/Message'), 
        where("ChatUserUID", "array-contains", props.CurrentUser.UID)),
        (snapshot) => {
            snapshot.forEach((doc) => {
                CounterpartMessageDocID.value = doc.id
        })
    })
    const docRef = doc(db, "Users", counterpartUID.value)
    const docSnap = await getDoc(docRef)
    if(docSnap.exists()) {
        DisplayName.value = docSnap.data().DisplayName
    }
}

const onClick = async() => {
    let result = window.confirm("채팅방을 개설하시겠습니까?")
    if(result) {
        await addDoc(collection(db, 'Users/' + counterpartUID.value + '/Message'), {
            ChatUserUID : [counterpartUID.value, props.CurrentUser.UID],
        })
        await addDoc(collection(db, 'Users/' + props.CurrentUser.UID + '/Message'), {
            ChatUserUID : [counterpartUID.value, props.CurrentUser.UID],
        })
        console.log("채팅방이 개설 되었습니다.")
    }
}

// const chekTrue = ref(true)
// const checkClick = () => {
//     console.log("check")
// }

const onClickBack = () => {
    hiden.value = false
}

const props = defineProps({
  CurrentUser: Object,
})

</script>

